@mixin respond-to($width) {
  @if $width == 1440 {
    @media (max-width: 1440px) {
      @content; } }

  @else if $width == 1200 {
    @media (max-width: 1200px) {
      @content; } }

  @else if $width == 1140 {
    @media (max-width: 1140px) {
      @content; } }

  @else if $width == 992 {
    @media (max-width: 992px) {
      @content; } }

  @else if $width == 768 {
    @media (max-width: 768px) {
      @content; } }

  @else if $width == 700 {
    @media (max-width: 700px) {
      @content; } }

  @else if $width == 576 {
    @media (max-width: 576px) {
      @content; } } }
